<template>
    <div class="Withdraw">
        <MyMenu />
        <a-row>
            <a-col :span="this.g.container.span" :offset="this.g.container.offset">

                <div v-if="!(authInfo && authInfo.status == 3)">
                    <router-link to="/my/authentication">
                        <h4><a href="#/my/authentication">Please verify your identity before applying for withdrawal!</a></h4>
                    </router-link>
                </div>

                <div v-if="authInfo && authInfo.status == 3">

                    <div class="bg-white" style="padding:15px;">
                        <a-row class="mtb30 t-left" type="flex">
                            <a-col flex="200px"> Select Withdraw Type：</a-col>
                            <a-col flex="auto">
                                <a-radio-group v-model="withdrawChargesDefault" :disabled="loading" @change="onRadioChange">
                                    <a-radio-button v-for="item in withdrawChargesList" :key="item.id" :value="item.label">
                                        {{item.label.toLocaleUpperCase()}}
                                    </a-radio-button>
                                </a-radio-group>
                            </a-col>
                        </a-row>

                        <a-row class="mtb30 t-left" type="flex" align="middle">
                            <a-col flex="200px">withdrawAddress：</a-col>
                            <a-col flex="auto">
                                <a-col :span="12">
                                    <a-input v-model="withdrawAddress" :disabled="loading" type="text" placeholder="Withdraw Address" />
                                    <a-alert v-if="withdrawAddressAlert" :message="withdrawAddressAlertText" banner />
                                </a-col>
                            </a-col>
                        </a-row>

                        <a-row class="mtb30 t-left" type="flex" align="middle">
                            <a-col flex="200px">Withdraw Amount：</a-col>
                            <a-col flex="auto">
                                <a-col :span="12">
                                    <a-input v-model="withdrawAmount" :disabled="loading" type="number" placeholder="Withdraw Amount" />
                                    <a-alert v-if="withdrawAmountAlert" :message="withdrawAmountAlertText" banner />
                                </a-col>
                                <a-col :span="12">
                                    <a-row type="flex" justify="space-around" align="middle">
                                        <a-col flex="220px" class="t-right" style="background-color:#FFF;">
                                            Account balance: <span class="t-red">{{accountBalance}}</span> {{withdrawChargesDefault.toLocaleUpperCase()}}
                                        </a-col>
                                        <a-col flex="100px" style="background-color:#FFF;">
                                            <a-button @click="onClickAllin" :disabled="loading" type="link">
                                                Allin
                                            </a-button>
                                        </a-col>
                                        <a-col flex="auto" style="background-color:#FFF;"></a-col>
                                    </a-row>
                                </a-col>
                            </a-col>
                        </a-row>

                        <a-row class="mtb30 t-left" type="flex" align="middle">
                            <a-col flex="200px">Charges：</a-col>
                            <a-col flex="auto">
                                {{withdrawCharges}}
                            </a-col>
                        </a-row>
                        <a-row class="mtb30 t-left" type="flex" align="middle">
                            <a-col flex="200px">Withdraw Amount：</a-col>
                            <a-col flex="auto">
                                <span v-if="withdrawAmount-withdrawCharges<=0">0</span>
                                <span v-if="withdrawAmount-withdrawCharges>0">{{withdrawAmount-withdrawCharges}}</span> {{withdrawChargesDefault.toLocaleUpperCase()}}
                            </a-col>
                        </a-row>
                        <a-row class="mtb30 t-left" type="flex" align="middle">
                            <a-col flex="auto">
                                <a-button @click="onClickSubmitApplyFor" type="primary" :loading="loading" block>
                                    Submit Apply for
                                </a-button>
                            </a-col>
                        </a-row>
                    </div>

                    <div class="mtb30" />

                    <a-row>
                        <a-col class="t-left">
                            <h5>Apply Record</h5>
                        </a-col>
                    </a-row>

                    <a-table :columns="columns" :dataSource="tableDatas" :loading="loading" :pagination="pagination" @change="handleTableChange" :rowKey="tableDatas => tableDatas.id" class="bg-white">
                        <template slot="status" slot-scope="text, record">
                            <div v-if="record.status == 2">
                                <!-- <a-popconfirm :title="record.failed_reason" @confirm="() => onDelete(record.key)">
                                    <a href="javascript:;">view</a>
                                </a-popconfirm> -->
                                <a-tooltip placement="topLeft">
                                    <template slot="title">
                                        <span>{{record.failed_reason}}</span>
                                    </template>
                                    <a-button type="danger" size="small" ghost>{{text}}</a-button>
                                </a-tooltip>
                            </div>
                            <div v-if="record.status != 2">
                                {{text}}
                            </div>

                        </template>

                    </a-table>
                </div>

            </a-col>

        </a-row>

    </div>
</template>


<script>
import MyMenu from "@/components/MyMenu.vue";
export default {
    name: "Withdraw",
    components: {
        MyMenu,
    },
    data() {
        return {
            loading: false,
            authInfo: null,
            withdrawChargesList: [],
            withdrawChargesDefault: 'btc',
            withdrawCharges: 0,

            withdrawAddress: "",
            withdrawAddressAlert: false,
            withdrawAddressAlertText: "",

            withdrawAmount: 0,
            withdrawAmountAlert: false,
            withdrawAmountAlertText: "",

            accountBalanceList: [],
            accountBalance: 0,

            tableDatas: [],
            pagination: {
                total: 0,
                pageSize: 5,
            },
            queryParam: {
                page: 1,
                size: 5,
                user_id: this.g.userInfo.id,
            },
            columns: [
                {
                    title: "Coin Type",
                    dataIndex: "type",
                    width: "10%",
                }, {
                    title: "Apply Amount",
                    dataIndex: "apply_amount",
                    width: "10%",
                }, {
                    title: "Real Amount",
                    dataIndex: "real_amount",
                    width: "10%",
                }, {
                    title: "Charges",
                    dataIndex: "charges",
                    width: "10%",
                }, {
                    title: "Address",
                    dataIndex: "address",
                }, {
                    title: "Status",
                    dataIndex: "statusText",
                    scopedSlots: { customRender: 'status' },
                    width: "20%",
                }, {
                    title: "Apply Time",
                    dataIndex: "create_datetime",
                    width: "20%",
                },
            ],

        };
    },
    methods: {
        onClickSubmitApplyFor() {
            // 提现地址不能为空
            if (this.withdrawAddress.trim() == "") {
                this.withdrawAddressAlert = true
                this.withdrawAddressAlertText = "Please enter your wallet address"
                return
            }
            // 提现数量不能小于手续费
            if (this.withdrawAmount - this.withdrawCharges <= this.withdrawCharges) {
                this.withdrawAmountAlert = true
                this.withdrawAmountAlertText = "Please enter a suitable value"
                return
            }
            // 提现数量不能大于 余额
            if (this.withdrawAmount > this.accountBalance) {
                this.withdrawAmountAlert = true
                this.withdrawAmountAlertText = "Please enter a suitable value"
                return
            }
            // 余额不足
            if (this.accountBalance <= 0) {
                this.$message.error('Insufficient Balance')
                return
            }

            this.withdrawAmountAlert = this.withdrawAddressAlert = false
            this.withdrawAmountAlertText = this.withdrawAddressAlertText = ""
            this.loading = true

            const url = `${this.g.apiUrl}/api/my/withdraw/applyfor`
            const body = {
                user_id: this.g.userInfo.id,
                withdrawCoinType: this.withdrawChargesDefault,
                withdrawAmount: this.withdrawAmount,
                withdrawAddress: this.withdrawAddress,
            }
            $.post(url, body, (res) => {
                console.log(res)
                this.loading = false
                if (res.flag != "ok") {
                    this.$message.error(res.flag)
                    return
                }
                this.$message.success('Submit success!')
                this.withdrawAddress = ""
                this.withdrawAmount = 0
                this.init()
            }).fail((err, e) => {
                console.log(err, e)
                this.$message.error(err.responseText)
                this.loading = false
            })
        },
        onClickAllin() {
            this.withdrawAmount = this.accountBalance
        },
        onRadioChange(e) {
            this.withdrawAmount = 0
            this.withdrawCharges = getCharges(this.withdrawChargesList, e.target.value).value
            this.accountBalance = getBalance(this.accountBalanceList, this.withdrawChargesDefault).assets_amount
        },
        handleTableChange(pagination) {
            this.pagination.current = pagination.current;
            this.pagination.pageSize = pagination.pageSize;
            this.queryParam.page = pagination.current;
            this.queryParam.size = pagination.pageSize;
            this.getTableList();
        },
        formatDate(list) {
            let a = list;
            a.forEach(function (item) {
                item.create_datetime = moment(item.create_datetime).format("YYYY-MM-DD HH:mm:ss");
                if (item.status == 1) {
                    item.statusText = "Applying "
                } else if (item.status == 2) {
                    item.statusText = "Reject"
                } else if (item.status == 3) {
                    item.statusText = "Pass"
                } else if (item.status == 4) {
                    item.statusText = "Successful withdrawal"
                }
            });
            return a;
        },
        getTableList() {
            $.get(`${this.g.apiUrl}/api/my/withdraw/history`, this.queryParam, (res) => {
                if (res.flag != "ok") {
                    //提示错误
                    this.$message.error(res.flag);
                    return;
                }

                const pagination = { ...this.pagination };
                pagination.total = res.data.page.total;
                this.tableDatas = this.formatDate(res.data.list);
                this.pagination = pagination;
            })
        },
        getWithdrawInfo() {
            $.get(`${this.g.apiUrl}/api/my/withdraw`, this.queryParam, (res) => {
                if (res.flag != "ok") {
                    //提示错误
                    this.$message.error(res.flag);
                    return;
                }

                this.authInfo = res.data.authInfo

                this.withdrawChargesList = res.data.withdrawCharges
                this.withdrawCharges = getCharges(this.withdrawChargesList, this.withdrawChargesDefault).value

                this.accountBalanceList = res.data.walletList
                this.accountBalance = getBalance(this.accountBalanceList, this.withdrawChargesDefault).assets_amount
            })
        },
        init() {
            this.getWithdrawInfo()
            this.getTableList()
        },
    },
    mounted() {
        this.init();
    },
};

function getCharges(arr, key) {
    for (let i = 0; i < arr.length; i++) {
        let item = arr[i]
        if (item.label == key) {
            return item
        }
    }
}
function getBalance(arr, key) {
    for (let i = 0; i < arr.length; i++) {
        let item = arr[i]
        if (item.name == key) {
            return item
        }
    }
}
</script>



<style scoped>
</style>